import React from 'react';
import cx from 'classnames';
import mime from 'mime-types';
import ImageLoader from 'react-imageloader';
import ReactPlayer from 'react-player';

import './MediaLoaderHelper.scss';
// import poster from '../../assets/overview.jpg';

class MediaLoaderHelper extends React.Component {
  constructor() {
    super();
    this.state = {
      play: false
    }
  }
  render() {
    let media = this.props.media;
    const attributes = this.props.attributes;
    const isBackground = attributes.isBackground;
    const mimeType = mime.lookup(media);
    const isAudio = mimeType && mimeType.indexOf("audio") >= 0;
    const isVideo = mimeType && mimeType.indexOf("video") >= 0;
    const isImage = mimeType && mimeType.indexOf("image") >= 0;
    const isClassName = !isVideo && !isImage;
    const className = cx({
      "MediaLoaderHelper": true,
      "MediaLoaderHelper-Video": isVideo,
      "MediaLoaderHelper-Image": isImage,
    });
    let style = {};
    if(isVideo || isAudio) {
      style = {
        paddingBottom: Math.pow(attributes.ratio, -1)*100 + '%'
      };
      media = <ReactPlayer
                url={media}
                playing={attributes.play}
                loop={attributes.loop}
                controls={attributes.controls}
                fileConfig={{ attributes: { controls: attributes.controls } }}

               />;
    }
    else if(isImage) {
      if(isBackground) {
        media = <div className="MediaLoaderHelper-BackgroundImage" style={{ backgroundImage: 'url('+media+')' }} />
      }
      else {
        media = <ImageLoader
                  src={media}
                  imgProps={{ alt: "" }}
                  preloader={this._preloader}
                />;
      }
    }
    else if(isClassName) {
      const mediaClassName = cx([
        "MediaLoaderHelper-BackgroundClass",
        "MediaLoaderHelper--"+media
      ])
      media = <div className={mediaClassName} />;
    }
    return (
      <div className={className} style={style}>
        {media}
      </div>
    );
  }
  _preloader() {
    return <div className="MediaLoaderHelper-Preloader"><div className="loader"></div></div>;
  }
}

MediaLoaderHelper.displayName = 'MediaLoaderHelper';

// Uncomment properties you need
MediaLoaderHelper.propTypes = {
  media: React.PropTypes.string.isRequired,
  attributes: React.PropTypes.shape({
    play: React.PropTypes.bool,
    ratio: React.PropTypes.number,
    loop: React.PropTypes.bool,
  })
};
MediaLoaderHelper.defaultProps = {
  attributes: {
    ratio: 16/9,
    controls: true,
    loop: false,
  }
};

export default MediaLoaderHelper;



