import React, { Component } from 'react';
import './Header.scss';

import GridHelper from './GridHelper';

class Header extends Component {
  render() {
    return (
      <GridHelper className='Header' size='wide' span={10}>
        <h1>
          Jure Martinec is a European designer and technologist, researching how digital might be shaping the world, often through code and interactive artifacts. He currently works at <a href='https://www.ideo.com/' target='_blank'>IDEO</a> and is with <a href='http://www.ansambel.org/' target='_blank'>Ansambel</a>. <a href='mailto:hello@jure.io'>Get in touch</a> and follow on <a href='https://twitter.com/youremartinets' target='_blank'>Twitter</a>, <a href='https://linkedin.com/in/juremartinec' target='_blank'>LinkedIn</a> or <a href='https://github.com/murej' target='_blank'>GitHub</a>.
        </h1>
      </GridHelper>
    );
  }
}

export default Header;



