import React, { Component } from 'react';
import isEmpty from 'lodash/isEmpty';
import './ProjectIndexPage.scss';

import ProjectHeader from './ProjectHeader';
import GridHelper from './GridHelper';

import projects from '../data/projects';

class ProjectIndexPage extends Component {

  render() {
    const projectLinks = projects.map( (project, i) => {
      if(!project.hidden) {
        const link = !isEmpty(project.route) ? { to: project.route } : {};
        const image = !isEmpty(project.media) && (Array.isArray(project.media) ? project.media[0] : project.media)
        return (
          <ProjectHeader
            link={link}
            title={project.title}
            caption={project.caption}
            image={image}
            isConfidential={project.confidential}
            key={i}
          />
        );
      }
    })

    return (
      <GridHelper className='ProjectIndexPage' span={9}>
        {projectLinks}
      </GridHelper>
    );
  }
}

export default ProjectIndexPage;



